import { mapState } from "vuex";
export default {
    name: "review-message",
    data() {
        return {
            type: 'share',
            first: true,
            over: false,
            titleShow: false,
            showForm: false,
            form: [],
            list: [],
            tab: [],
            getFocus: false,
            keyword: '',
            searchBool: false,
            modelType: 1,
            reasonRefusal: '',
            setItem: {},
            titleWidth: '100%',
            model: false,
            page: 1
        }
    },
    computed: {
        ...mapState({
            windowHeight : state=>state.gConfig.systemInfo.windowHeight
        })
    },
    onLoad() { this.$commonLoad.onload();
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.getTabs();
    },
    onShow() {
        if(this.first) {
            return false
        }
        this.request({
            key: this.type,
            page: 1,
            keyword: this.keyword,
        }).then(response => {
            if (response) {
                this.list = response.list;
            }
        });
    },
    methods: {
        getMore() {
            if (!this.over) {
                this.page++;
                this.request({
                    key: this.type,
                    page: this.page,
                    keyword: this.keyword,
                }).then(response => {
                    if (response && response.list.length > 0) {
                        this.list = [...this.list, ...response.list];
                    } else if (response && response.list.length === 0) {
                        this.over = true;
                    }
                });
            }
        },
        imageLoad(index) {
            this.form[index].loadOver = false;
        },
        // 查看图片
        look(e) {
            uni.previewImage({
                current: e, // 当前显示图片的http链接
                urls: [e] // 需要预览的图片http链接列表
            })
        },
        toggle(item) {
            this.form = item.form;
            for(let i in this.form) {
                if(this.form[i].key == 'img_upload' &&  Array.isArray(this.form[i].value)) {
                    let show = false;
                    for(let img of this.form[i].value) {
                        if(img != '') {
                            show = true;
                        }
                    }
                    if(!show) {
                        this.form.splice(i,1)
                    }
                }
            }
            this.showForm = true;
        },
        toUrl(item) {
            if(this.type == 'region' || this.type == 'community' || this.type == 'mch') {
                let url = '/pages/app_admin/'+this.type+'-detail/'+this.type+'-detail?id=';
                if(this.type == 'region') {
                    url += item.id + '&name=' + item.nickname
                }
                if(this.type == 'mch') {
                    url += item.id
                }
                if(this.type == 'community') {
                    url += item.user_id
                }
                uni.navigateTo({
                    url: url
                });
            }
        },
        tabSwitch(type) {
            this.list = [];
            this.type = type;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.request({
                key: type,
                page: 1,
                keyword: '',
            }).then(response => {
                uni.hideLoading();
                if (response) {
                    this.list = response.list;
                }
            });
        },
        getTabs() {
            let that = this;
            that.$request({
                url: that.$api.app_admin.tabs_v2,
                method: 'get'
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.tab = response.data;
                    if(that.tab.length == 1) {
                        uni.setNavigationBarTitle({
                            title: that.tab[0].plugin + '审核消息'
                        })
                    }else {
                        that.titleShow = true;
                    }
                    that.titleWidth = 1/that.tab.length * 100 + '%';
                    that.type = response.data[0].key;
                    that.request({
                        key: response.data[0].key,
                        page: 1,
                        keyword: '',
                    }).then(response => {
                        this.first = false;
                        if (response) {
                            that.list = response.list;
                        }
                    })
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            })
        },
        blurFocus() {
            let that = this;
            setTimeout(v =>{
                that.getFocus=false;
                that.searchText();
            },300);
        },

        clearSearch() {
            this.keyword = '';
            this.searchBool = false;
            if(!this.searchBool) {
                this.searchText();
            }
        },

        changeType(data) {
            this.list = [];
            this.page = 1;
            this.type = data;
            this.over = false;
            this.keyword = '';
            this.searchBool = false;
            this.request({
                key: data,
                page: this.page,
                keyword: this.keyword,
            }).then(response => {
                if (response) {
                    this.list = response.list;
                }
            });
        },
        async request({key, page, keyword}) {
            const response = await this.$request({
                url: this.$api.app_admin.review_v2,
                data: {
                    key: key,
                    page: page,
                    keyword: keyword
                }
            });
            if (response.code === 0) {
                return response.data;
            } else {
                uni.showToast({
                    title: response.msg,
                    icon: 'none',
                    duration: 1000
                });
                return false;
            }
        },
        checkQueue(queue_id) {
            this.$request({
                url: this.$api.app_admin.queue,
                data: {
                    queue_id: queue_id,
                }
            }).then(response => {
                if (response.retry && response.retry == 1) {
                    this.checkQueue(queue_id);
                }else {
                    for (let i = 0; i < this.list.length; i++) {
                        if (this.list[i].id === this.setItem.id) {
                            this.$delete(this.list, i);
                            this.model=false;
                            this.setItem = {};
                        }
                    }
                }
            })
        },
        searchText() {
            if (this.keyword === '') {
                this.searchBool = false;
            }
            this.page = 1;
            this.over = false;
            uni.showLoading({
                mask: true,
                title: '搜索中...'
            });
            this.request({
                key: this.type,
                page: this.page,
                keyword: this.keyword,
            }).then(response => {
                uni.hideLoading();
                if (response) {
                    this.list = response.list;
                }
            });
        },
        refuse(data) {
            this.setItem = data;
            this.modelType = 1;
            this.model = true;
        },
        by(data) {
            this.setItem = data;
            this.modelType = 2;
            this.model = true;
        },
        confirm() {
            if (this.modelType === 2) {
                this.$request({
                    url: this.$api.app_admin.review_switch_v2,
                    method: 'post',
                    data: {
                        key: this.type,
                        status: 1,
                        form: JSON.stringify(this.setItem),
                        user_id: this.setItem.user_id
                    }
                }).then(response => {
                    if (response.code === 0) {
                        if(this.type == 3) {
                            this.checkQueue(response.data.queue_id);
                        }else {
                            for (let i = 0; i < this.list.length; i++) {
                                if (this.list[i].id === this.setItem.id) {
                                    this.$delete(this.list, i);
                                    this.model=false;
                                    this.setItem = {};
                                }
                            }
                        }
                    }
                })
            } else if (this.modelType === 1) {
                this.setItem.review_remark = this.reasonRefusal;
                this.$request({
                    url: this.$api.app_admin.review_switch_v2,
                    method: 'post',
                    data: {
                        key: this.type,
                        status: 2,
                        form: JSON.stringify(this.setItem),
                        reason: this.reasonRefusal,
                        user_id: this.setItem.user_id
                    }
                }).then(response => {
                    if (response.code === 0) {
                        this.reasonRefusal = '';
                        for (let i = 0; i < this.list.length; i++) {
                            if (this.list[i].id === this.setItem.id) {
                                this.$delete(this.list, i);
                                this.model=false;
                                this.setItem = {};
                            }
                        }
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                });
            }
        },
        cancel() {
            this.model=false;
            this.form = [];
            this.showForm = false;
            this.setItem = {};
        }
    }
}
